import React from "react";
import styles from "./styles";
import { NavLink } from "react-router-dom";
import "./navbar.css";
import logo from "./logo.png";

const Navbar = () => {
  return (
    <>
      <nav className="navbar navbar-expand-lg navbar-light bg-warning">
        <div className="container-fluid">
          <img src={logo} classNameName="logo" style={styles.logo}></img>
          <h5 className="navbar-brand Name" style={styles.Name}>
            <strong>The Yuva Unity Foundation</strong>
          </h5>
          <button
            className="navbar-toggler"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <NavLink
                  className="nav-link active navlink"
                  style={styles.navlink}
                  aria-current="page"
                  to="/Home"
                >
                  Home
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink
                  className="nav-link navlink"
                  style={styles.navlink}
                  to="/About"
                >
                  About Us
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink
                  className="nav-link navlink"
                  style={styles.navlink}
                  to="/Contact"
                >
                  Contact Us
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink
                  className="nav-link navlink"
                  style={styles.navlink}
                  to="/Achievements"
                >
                  Achievements
                </NavLink>
              </li>
              <li className="nav-item dropdown navlink" style={styles.NavLink}>
                <NavLink
                  className="nav-link dropdown-toggle navlink"
                  style={styles.navlink}
                  to="#"
                  id="navbarDropdown"
                  role="button"
                  data-bs-toggle="dropdown"
                  aria-expanded="false"
                >
                  More
                </NavLink>
                <ul className="dropdown-menu" aria-labelledby="navbarDropdown">
                  <li>
                    <NavLink className="dropdown-item" to="/Lockdown">
                      Lock down
                    </NavLink>
                  </li>
                  <li>
                    <NavLink className="dropdown-item" to="/Gallery">
                      Photo Gallery
                    </NavLink>
                  </li>
                  <li>
                    <hr className="dropdown-divider" />
                  </li>
                  <li>
                    <NavLink className="dropdown-item" to="#">
                      Something else here
                    </NavLink>
                  </li>
                </ul>
              </li>
            </ul>
          </div>
        </div>
      </nav>{" "}
    </>
  );
};

export default Navbar;
